// pages/index.js
// import React from 'react'
import {Component} from 'react'
import Layout from '../components/MyLayout.js'
import HeaderCategory from '../components/HeaderCategory.js'
// import workshops from '../data/WorkshopData.js'
import ProgramDescription from '../components/programDescription'
import program from '../data/programDataTest.js'

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    // this is where i Put my code?
    let testP = program[0];
    return (
      <Layout>
        <HeaderCategory />
        <div className="grid">
          <div className="col-1-1 floatLeft">
            <p>This is some frontpage content | {testP.summary}</p>
          </div>
        </div>
        {program.map( function(prg, i) {
          return <ProgramDescription
            key={prg.id}
            id={prg.id}
            shortUrl={prg.shortUrl}
            title={prg.title}
            lead={prg.lead}
            summary={prg.summary}
            />
        })}
      </Layout>
    )
  }
}

